@import '../utils/css-variables';


.Map {
    height: 100vh;
    overflow: hidden;
    display: flex;

    &__list {
        flex: 0 1 300px;
        overflow: auto;
    }
    &__wrapper {
        flex: 1 0 auto;
        height: 100%;
    }
    &__holder {
        height: 100%;
    }

    &__loader {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        -webkit-backdrop-filter: blur(5px);
        background: rgba(255,255,255, .5) inlineSvg($svg-load) center no-repeat;
        background-size: 50px 50px;
    }

    &__hint {
        margin-top: 5px;
        h4 {
            font-size: 14px;
            margin-top: 0;
            margin-bottom: 0.4em;
        }
        
    }

    &__location {
        opacity : .7;
        span {
            display: inline-block;
            margin-right: 0.5em;

            max-width: 150px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
}