//colors
$c-grey: #eaeaea;
$c-grey-dark: darken($c-grey, 5%);
$c-grey-light: lighten($c-grey, 2);

$c-success: #c6e9c7;
$c-success-light: lighten(#c6e9c7, 4);

$c-hl: #f39c11;

// base64

$svg-load: '<svg width="100" height="99" viewBox="0 0 100 99" xmlns="http://www.w3.org/2000/svg"  preserveAspectRatio="xMidYMid"> <defs> <radialGradient cx="72.443%" cy="0%" fx="72.443%" fy="0%" r="100%" id="a"> <stop stop-color="#{$c-hl}" stop-opacity="0" offset="0%" /> <stop stop-color="#{$c-hl}" offset="4.572%" /> <stop stop-color="#{$c-hl}" stop-opacity=".744" offset="54.279%" /> <stop stop-color="#{$c-hl}" stop-opacity="0" offset="100%" /> </radialGradient> </defs><g><animateTransform attributeName="transform" type="rotate" from="0 50 50" to="360 50 50" keyTimes="0;1" dur="1.4s" repeatCount="indefinite" /><path d="M50 100C22.386 100 0 77.614 0 50S22.386 0 50 0s50 22.386 50 50-22.386 50-50 50zm0-5c24.853 0 45-20.147 45-45S74.853 5 50 5 5 25.147 5 50s20.147 45 45 45z" fill="url(#a)" fill-rule="nonzero" /></g></svg>';


// Utils


@function inlineSvg($svg){
    //
    //  Add missing namespace
    //
    @if not str-index($svg,xmlns) {
        $svg: str-replace($svg, '<svg','<svg xmlns="http://www.w3.org/2000/svg"');   
    }        
    //    
    //  Chunk up string in order to avoid 
    //  "stack level too deep" error
    //     
    $encoded:'';
    $slice: 2000;
    $index: 0;
    $loops: ceil(str-length($svg)/$slice);
    @for $i from 1 through $loops {
        $chunk: str-slice($svg, $index, $index + $slice - 1); 
        //
        //   Encode 
        //
        $chunk: str-replace($chunk,'"', '\'');
        $chunk: str-replace($chunk,'%', '%25');
        $chunk: str-replace($chunk,'&', '%26');
        $chunk: str-replace($chunk,'#', '%23');       
        $chunk: str-replace($chunk,'{', '%7B');
        $chunk: str-replace($chunk,'}', '%7D');         
        $chunk: str-replace($chunk,'<', '%3C');
        $chunk: str-replace($chunk,'>', '%3E');   
        $encoded: #{$encoded}#{$chunk};
        $index: $index + $slice; 
    }
    @return url("data:image/svg+xml,#{$encoded}");   
  }
  
  //  Helper function to replace characters in a string
  @function str-replace($string, $search, $replace: '') {
    $index: str-index($string, $search); 
    @return if($index, 
        str-slice($string, 1, $index - 1) + $replace + 
        str-replace(str-slice($string, $index + 
        str-length($search)), $search, $replace), 
        $string); 
  }