@import '../utils/css-variables';
.List {
    list-style: none;
    padding: 0;
    margin: 0;
    &__item {
        background-color: $c-grey;
        margin: 0;
        height: 60px;
        border-bottom: 1px solid $c-grey-dark;
        padding: .5em;

        &:nth-child(even) {
            background-color: $c-grey-light;
        }

        h4 {
            margin: 0;
            font-size: 16px;
            margin-bottom: 0.5em;
            line-height: 1;
            font-weight: 500;
        }

        span {
            margin: 0;
            display: block;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            opacity: .7;
            font-size: 14px;
        }

        &.loaded {
            animation: loaded 1s ease-out both;
        }
    }

}

@keyframes loaded {
    from {
        background-color: $c-success-light;
    }
}